html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  font-family: var(--font-poppins);
}

ul {
  list-style-type: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

:root {
  --cream: #fffbf8;
  --dark: #333333;
  --hover: #F4AE3E;
}

:root {
  --font-poppins: "Poppins", sans-serif;
}

.container {
  max-width: 68.75rem;
  margin: 0 auto;
  padding: 0 1.6rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0 6rem;
  }
}
@media (min-width: 87.5em) {
  .container {
    max-width: 81.25rem;
    padding: 0;
  }
}

.flex {
  display: flex;
  flex-direction: row;
}
.flex--j-c {
  justify-content: center;
}
.flex--j-sb {
  justify-content: space-between;
}
.flex--a-c {
  align-items: center;
}

h1,
h2,
h3 {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.button {
  position: relative;
  background-color: var(--cream);
  border: 0;
  padding: 1rem 2rem;
  margin-top: 1rem;
  color: var(--dark);
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  font-weight: 800;
  font-size: 0.625rem;
  transition: transform 0.3s cubic-bezier(0.33, 1, 0.68, 1);
}
.button:hover {
  cursor: pointer;
  background-color: var(--hover);
  color: var(--cream);
}
@media (min-width: 64em) {
  .button {
    margin-top: 1rem;
  }
}

.header__navbar {
  height: 5rem;
}
.header__logo {
  position: relative;
  z-index: 15;
  font-size: 1.5rem;
}
@media (min-width: 64em) {
  .header__nav-links {
    width: 40vw;
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 87.5em) {
  .header__nav-links {
    width: 28vw;
    display: flex;
    justify-content: space-between;
  }
}
.header__nav-item {
  color: inherit;
  position: relative;
  z-index: 10;
  font-size: 0.875rem;
}
@media (min-width: 87.5em) {
  .header__nav-item {
    font-size: 1rem;
  }
}
.header__nav-item::after {
  content: "";
  position: absolute;
  margin: auto;
  bottom: -0.1875rem;
  left: 0;
  height: 0.125rem;
  width: 0;
  background-color: var(--cream);
  transition: width 0.3s ease-in-out;
}
.header__nav-item:hover::after {
  width: 100%;
}
.header__nav-menu-icon-wrapper {
  height: 2rem;
  width: 2rem;
  cursor: pointer;
}
@media (min-width: 64em) {
  .header__nav-menu-icon-wrapper {
    display: none;
  }
}
.header__nav-menu-icon {
  position: relative;
  z-index: 10;
  width: 1.6rem;
  height: 0.15rem;
  background-color: var(--cream);
  border-radius: 5px;
  transition: background-color 0.3s linear;
}
.header__nav-menu-icon.active:nth-of-type(1) {
  background-color: transparent;
}
.header__nav-menu-icon::before {
  content: "";
  position: absolute;
  top: -0.4375rem;
  width: inherit;
  height: inherit;
  background-color: var(--cream);
  border-radius: inherit;
  transition: transform 0.3s linear, top 0.3s linear;
}
.header__nav-menu-icon.active::before {
  background-color: var(--cream);
  transform: rotate(45deg);
  top: 0;
}
.header__nav-menu-icon::after {
  content: "";
  position: absolute;
  top: 0.4375rem;
  width: inherit;
  height: inherit;
  background-color: var(--cream);
  border-radius: inherit;
  transition: transform 0.3s linear, top 0.3s linear;
}
.header__nav-menu-icon.active::after {
  top: 0;
  transform: rotate(-45deg);
}
.header__mobile-nav-menu {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  padding: 10rem 6rem 10rem 0;
  opacity: 0;
  z-index: -1;
  visibility: hidden;
  transition: all 0.3s;
  background-color: rgba(0, 0, 0, 0.4);
}
.header__mobile-nav-menu-wrapper {
  height: 100%;
  flex-direction: column;
  align-items: flex-end;
  transform: skew(-14deg) translateX(-18%);
}
.header__mobile-nav-menu.active {
  opacity: 1;
  z-index: 5;
  visibility: visible;
}
.header__mobile-nav-menu::before {
  content: "";
  position: absolute;
  background-color: var(--dark);
  top: 0;
  bottom: 0;
  right: -0.9375rem;
  left: 0;
  transform-origin: 0 0;
  transform: skew(-14deg) translateX(-110%);
  transition: transform 0.2s ease-in-out;
}
.header__mobile-nav-menu.active::before {
  transform: skew(-14deg) translateX(0%);
}
.header__mobile-nav-item {
  position: relative;
  color: var(--cream);
  font-size: 1.875rem;
  visibility: hidden;
  opacity: 0;
  transform: translateY(0.625rem);
}
.header__mobile-nav-item:nth-child(1) {
  transition: all 0.3s 175ms;
}
.header__mobile-nav-item:nth-child(2) {
  transition: all 0.3s 225ms;
}
.header__mobile-nav-item:nth-child(3) {
  transition: all 0.3s 275ms;
}
.header__mobile-nav-item:nth-child(4) {
  transition: all 0.3s 325ms;
}
.header__mobile-nav-item:nth-child(5) {
  transition: all 0.3s 375ms;
}
.header__mobile-nav-item:nth-child(6) {
  transition: all 0.3s 425ms;
}
.header__mobile-nav-item.active {
  transform: translateY(0) skew(14deg);
  visibility: visible;
  opacity: 1;
}

.header {
  position: relative;
  overflow-x: hidden;
  color: var(--cream);
  height: 100vh;
  width: 100vw;
}
.header__overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.15);
}
.header__wrapper {
  height: 60vh;
  position: absolute;
  bottom: 10%;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 64em) {
  .header__wrapper {
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: start;
    flex-direction: row;
  }
}
.header__social {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  height: 18vh;
}
@media (min-width: 64em) {
  .header__social {
    align-items: center;
    width: auto;
    margin-right: 7.5rem;
    height: auto;
  }
  .header__social::before {
    content: "";
    height: 25vh;
    width: 0.09375rem;
    background-color: var(--cream);
  }
  .header__social::after {
    content: "";
    height: 25vh;
    width: 0.09375rem;
    background-color: var(--cream);
  }
}
.header__social-icon {
  height: 1.75rem;
  width: 1.75rem;
  fill: var(--cream);
  transition: transform 0.3s cubic-bezier(0.33, 1, 0.68, 1);
}
.header__social-icon:hover {
  cursor: pointer;
  fill: var(--hover);
}
@media (min-width: 64em) {
  .header__social-icon {
    margin-bottom: 1.75rem;
  }
}
@media (min-width: 64em) {
  .header__social-icon:first-of-type {
    margin-top: 1.75rem;
  }
}
.header__personal {
  order: -1;
  align-self: flex-start;
}
.header__personal h1 {
  font-size: 3rem;
  line-height: 1;
  margin-bottom: 0rem;
}
@media (min-width: 64em) {
  .header__personal h1 {
    font-size: 3.5rem;
  }
}
@media (min-width: 87.5em) {
  .header__personal h1 {
    font-size: 4.625rem;
  }
}
.header__personal h2 {
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: 0.0625rem;
}
@media (min-width: 64em) {
  .header__personal h2 {
    font-size: 1.625rem;
  }
}
@media (min-width: 87.5em) {
  .header__personal h2 {
    font-size: 2.25rem;
  }
}
@media (min-width: 64em) {
  .header__personal {
    order: 0;
    align-self: auto;
  }
}
.header::after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background-image: url("../images/header-4.jpg");
  background-position: 46% 0;
  background-size: cover;
  transform: scaleX(-1);
}
@media (min-width: 64em) {
  .header::after {
    background-position: 100% bottom;
  }
}
@media (min-width: 87.5em) {
  .header::after {
    background-position: center bottom;
  }
}